/* BEGIN PT Sans Narrow */
@font-face {
  font-family: 'PT Sans Narrow';
  src: url('../fonts/PTSans-Narrow.eot'); /* IE9 Compat Modes */
  src: url('../fonts/\
  PTSans-Narrow.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/PTSans-Narrow.woff2') format('woff2'), /* Modern Browsers */
    url('../fonts/PTSans-Narrow.woff') format('woff'); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'PT Sans Narrow';
  src: url('../fonts/PTSans-NarrowBold.eot'); /* IE9 Compat Modes */
  src: url('../fonts/PTSans-NarrowBold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/PTSans-NarrowBold.woff2') format('woff2'), /* Modern Browsers */
    url('../fonts/PTSans-NarrowBold.woff') format('woff'); /* Modern Browsers */
  font-weight: bold;
  font-style: normal;
}
